import { useState, useEffect } from 'react';

/**
 * useLocalStorage - ReactlocalStorage
 *
 * @param {string} key - localStorage 
 * @param {*} initialValue - localStorage 
 * @returns {[any, Function]} - [, ]
 */
function useLocalStorage(key, initialValue) {
  // useStatelocalStorageinitialValue
  const [value, setValue] = useState(() => {
    const savedValue = localStorage.getItem(key); // localStorage
    return savedValue ? JSON.parse(savedValue) : initialValue; // JSON
  });

  // localStorage
  useEffect(() => {
    localStorage.setItem(key, JSON.stringify(value)); // JSON
  }, [key, value]); // keyvalue

  // ReactuseState
  return [value, setValue];
}

export default useLocalStorage;
