import { useCallback, useState, useEffect } from 'react';

/**
 * useCamera - 
 *
 * @param {RefObject} videoRef -  video 
 * @param {RefObject} canvasRef -  canvas 
 * @param {Function} onCapture - File 
 */
function useCamera(videoRef, canvasRef, onCapture) {
  // 
  const [devices, setDevices] = useState([]);
  // ID
  const [currentDeviceId, setCurrentDeviceId] = useState('');

  /**
   * 
   */
  const stopCamera = useCallback(() => {
    if (videoRef.current && videoRef.current.srcObject) {
      const stream = videoRef.current.srcObject;
      const tracks = stream.getTracks(); // /
      tracks.forEach(track => track.stop()); // 
      videoRef.current.srcObject = null; // 
    }
  }, [videoRef]);

  /**
   * 
   * @param {string} deviceId -  deviceIddeviceId
   */
  const startCamera = useCallback(async (deviceId = currentDeviceId) => {
    try {
      if (videoRef.current && videoRef.current.srcObject) {
        stopCamera(); // 
      }

      // ID
      const constraints = {
        video: deviceId ? { deviceId: { exact: deviceId } } : true
      };
      
      // 
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      
      if (videoRef.current) {
        videoRef.current.srcObject = stream; //  video 
        setCurrentDeviceId(deviceId); //  deviceId 
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  }, [videoRef, currentDeviceId, stopCamera]);

  /**
   * 
   */
  const getVideoDevices = useCallback(async () => {
    try {
      const devices = await navigator.mediaDevices.enumerateDevices(); // 
      const videoDevices = devices.filter(device => device.kind === 'videoinput'); // 
      setDevices(videoDevices); // 

      // 
      if (videoDevices.length > 0 && !currentDeviceId) {
        setCurrentDeviceId(videoDevices[0].deviceId);
      }

      return videoDevices;
    } catch (error) {
      console.error(':', error);
      return [];
    }
  }, [currentDeviceId]);

  /**
   * 
   */
  useEffect(() => {
    getVideoDevices();
  }, [getVideoDevices]);

  /**
   * canvas
   */
  const capturePhoto = useCallback(() => {
    if (videoRef.current && canvasRef.current) {
      const video = videoRef.current;
      const canvas = canvasRef.current;
      const context = canvas.getContext('2d');

      // canvas  video 
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;

      // video 
      context.drawImage(video, 0, 0, canvas.width, canvas.height);

      // base64 URL
      const imageData = canvas.toDataURL('image/jpeg');

      // base64  Blob  File 
      fetch(imageData)
        .then(res => res.blob())
        .then(blob => {
          const file = new File([blob], 'camera-photo.jpg', { type: 'image/jpeg' });
          if (onCapture) {
            onCapture(file); // 
          }
        });
    }
  }, [videoRef, canvasRef, onCapture]);

  /**
   *  deviceId 
   * @param {string} deviceId - ID
   */
  const switchCamera = useCallback(async (deviceId) => {
    if (deviceId && deviceId !== currentDeviceId) {
      await startCamera(deviceId);
    }
  }, [currentDeviceId, startCamera]);

  /**
   * 
   */
  const switchToNextCamera = useCallback(async () => {
    if (devices.length <= 1) return; // 1

    const currentIndex = devices.findIndex(device => device.deviceId === currentDeviceId);
    const nextIndex = (currentIndex + 1) % devices.length;
    await switchCamera(devices[nextIndex].deviceId);
  }, [devices, currentDeviceId, switchCamera]);

  // 
  return { 
    startCamera,             // 
    stopCamera,              // 
    capturePhoto,            // 
    switchCamera,            // 
    switchToNextCamera,      // 
    devices,                 // 
    currentDeviceId          // ID
  };
}

export default useCamera;
